.github-info {
  display: flex;
  margin: 0 44px 0 20px;
}
.github-info div {
  display: flex;
  align-items: center;
  padding: 3px 10px;
  background-color: #191919;
  border: 1px solid #191919;
  border-radius: 4px;
  font-size: 12px;
  color: #fff;
}
.github-info div.all-stars {
  margin-right: 7px;
  position: relative;
}
.github-info div.all-stars:after {
  content: "";
  width: 8px;
  height: 8px;
  background-color: #191919;
  transform: rotate(45deg) translateY(-50%);
  position: absolute;
  top: 50%;
  right: -1px;
}
.github-info div.all-stars img {
  margin-right: 10px;
}

@media only screen and (max-width: 699px) {
  .github-info {
    margin-right: 30px;
  }
}
